import React, { useEffect } from 'react';
import { HashRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { theme } from './styles/theme';
import { GlobalStyles } from './styles/GlobalStyles';
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';
import Home from './pages/Home';
import VideoEditing from './pages/VideoEditing';
import Designing from './pages/Designing';
import WebSupport from './pages/WebSupport';
import CreatorOutreach from './pages/CreatorOutreach';
import Careers from './pages/Careers';

// ScrollToTop component to ensure pages start at the top when navigating
function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <Router>
        <ScrollToTop />
        <Header />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/video-editing" element={<VideoEditing />} />
          <Route path="/designing" element={<Designing />} />
          <Route path="/website-support" element={<WebSupport />} />
          <Route path="/creator-outreach" element={<CreatorOutreach />} />
          <Route path="/careers" element={<Careers />} />
        </Routes>
        <Footer />
      </Router>
    </ThemeProvider>
  );
}

export default App;
