import React from 'react';
import styled, { css } from 'styled-components';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

// Base button styles shared by all variants
const baseButtonStyles = css`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-family: ${props => props.theme.fonts.body};
  font-weight: ${props => props.theme.fontWeights.semibold};
  font-size: ${props => props.size === 'large' ? props.theme.fontSizes.large : 
    props.size === 'small' ? props.theme.fontSizes.small : 
    props.theme.fontSizes.medium};
  line-height: 1;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  border-radius: ${props => props.pill ? props.theme.borderRadius.pill : props.theme.borderRadius.medium};
  padding: ${props => props.size === 'large' ? '1rem 2.5rem' : 
    props.size === 'small' ? '0.5rem 1rem' : 
    '0.75rem 1.5rem'};
  width: ${props => props.fullWidth ? '100%' : 'auto'};
  position: relative;
  overflow: hidden;
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  svg {
    transition: transform 0.3s ease;
  }
  
  &:hover:not(:disabled) svg {
    transform: ${props => props.iconPosition === 'right' ? 'translateX(4px)' : 
      props.iconPosition === 'left' ? 'translateX(-4px)' : 'scale(1.2)'};
  }
`;

// Primary button styles
const PrimaryButton = styled.button`
  ${baseButtonStyles};
  background: ${props => props.theme.gradients.primary};
  color: ${props => props.theme.colors.lightText};
  box-shadow: ${props => props.theme.shadows.primary};
  
  &:hover:not(:disabled) {
    transform: translateY(-3px);
    box-shadow: ${props => props.theme.shadows.primaryHover};
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
  }
`;

// Secondary button styles
const SecondaryButton = styled.button`
  ${baseButtonStyles};
  background: transparent;
  color: ${props => props.theme.colors.primary};
  border: 2px solid ${props => props.theme.colors.primary};
  
  &:hover:not(:disabled) {
    background: rgba(0, 255, 136, 0.1);
    transform: translateY(-3px);
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
  }
`;

// Tertiary button styles (text only)
const TertiaryButton = styled.button`
  ${baseButtonStyles};
  background: transparent;
  color: ${props => props.theme.colors.primary};
  padding: ${props => props.size === 'large' ? '0.75rem 1.5rem' : 
    props.size === 'small' ? '0.25rem 0.5rem' : 
    '0.5rem 1rem'};
  
  &:hover:not(:disabled) {
    background: rgba(0, 255, 136, 0.1);
    transform: translateY(-3px);
  }
  
  &:active:not(:disabled) {
    transform: translateY(-1px);
  }
`;

// Button component that chooses the right styled component based on variant
const Button = ({
  children,
  variant = 'primary',
  size = 'medium',
  icon,
  iconPosition = 'right',
  pill = false,
  as = 'button',
  to,
  href,
  fullWidth = false,
  onClick,
  type = 'button',
  disabled = false,
  ...props
}) => {
  // Choose the styled component based on variant
  const ButtonComponent = 
    variant === 'primary' ? PrimaryButton : 
    variant === 'secondary' ? SecondaryButton : 
    TertiaryButton;
  
  // Determine whether to use a Link, anchor, or button
  const buttonProps = {
    as: to ? Link : href ? 'a' : as,
    to, // For React Router Link
    href, // For anchor tags
    size,
    pill,
    fullWidth,
    onClick,
    type,
    disabled,
    iconPosition,
    ...props
  };
  
  return (
    <ButtonComponent {...buttonProps}>
      {icon && iconPosition === 'left' && <FontAwesomeIcon icon={icon} />}
      {children}
      {icon && iconPosition === 'right' && <FontAwesomeIcon icon={icon} />}
    </ButtonComponent>
  );
};

export default Button; 